.page__body {
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: @black;
  background-color: @white;
}

.page__body a {
  text-decoration: none;
}

.page__body button {
  outline: none;
  border: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-main {
  max-width: 100%;
  overflow-x: hidden;
}

.subtitle {
  position: relative;
  display: inline-block;
  vertical-align: baseline;
  margin: 0;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  font-family: "Oswald", "Arial", sans-serif;
  color: @extra-black;
  background: @white;
}

.subtitle::after,
.subtitle::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100vw;
  height: 3px;
  margin-top: calc(1em / 2 + 3px);
  background: @green;
  z-index: -1;
}

.subtitle::before {
  display: none;
}
