@import "global/normalize.css";
@font-face {
  font-family: "Oswald";
  src: url("../../fonts/oswaldregular.woff2") format("woff2"), url("../../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../../fonts/oswaldmedium.woff2") format("woff2"), url("../../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.page__body {
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
}
.page__body a {
  text-decoration: none;
}
.page__body button {
  outline: none;
  border: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-main {
  max-width: 100%;
  overflow-x: hidden;
}
.subtitle {
  position: relative;
  display: inline-block;
  vertical-align: baseline;
  margin: 0;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #111111;
  background: #ffffff;
}
.subtitle::after,
.subtitle::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100vw;
  height: 3px;
  margin-top: calc(1em / 2 + 3px);
  background: #5eaa2f;
  z-index: -1;
}
.subtitle::before {
  display: none;
}
.page-header {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
.page-header--index {
  min-height: 545px;
}
.page-header__logo {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  margin-top: 12px;
  margin-bottom: 11px;
  margin-left: 20px;
}
.page-header__logo:hover {
  opacity: 0.8;
  cursor: pointer;
}
.page-header__logo:active,
.page-header__logo:focus {
  opacity: 0.6;
  cursor: pointer;
}
.page-header__img {
  width: 191px;
  height: 39px;
}
.page-header__wrapper {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
}
.page-main__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: #000000;
  text-align: left;
  box-sizing: border-box;
  padding-top: 24px;
  padding-bottom: 41px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0;
  border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
}
.page-main__title--form {
  border-bottom: none;
  padding-bottom: 0;
}
.main-nav {
  background: #ffffff;
  margin: 0;
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  left: auto;
  background-color: #ffffff;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 23px;
  right: 20px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  left: auto;
  right: 20px;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 32px;
  right: 2px;
  width: 20px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__wrapper {
  width: 100%;
}
.site-list {
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
}
.site-list__item {
  box-sizing: border-box;
  padding-top: 21px;
  padding-bottom: 21px;
  text-align: center;
  border-top: 1px solid #e6e6e6;
}
.site-list__link {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  color: #000000;
  text-transform: uppercase;
}
.site-list__link:hover {
  opacity: 0.6;
  cursor: pointer;
}
.site-list__link:active,
.site-list__link:focus {
  opacity: 0.3;
  cursor: pointer;
}
.program {
  padding: 0 20px;
  z-index: 2;
}
.program__wrapper {
  margin-top: 25px;
}
.program b {
  display: inline-block;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: #ffffff;
  text-align: center;
  margin-bottom: 25px;
}
.program p {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  padding: 0;
  margin: 0;
}
.program__img {
  margin-top: 30px;
  width: 280px;
  height: 270px;
}
.program__button {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
  padding-top: 10px;
  padding-right: 26px;
  padding-bottom: 12px;
  padding-left: 26px;
  margin: 0 auto;
  margin-top: 0;
  background-color: #5eaa2f;
  display: flex;
  justify-content: center;
}
.program__button:hover {
  opacity: 0.7;
  cursor: pointer;
}
.program__button:active,
.program__button:focus {
  opacity: 0.5;
  cursor: pointer;
}
.program-form {
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 35px;
}
.program-form__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #000000;
  text-transform: uppercase;
  margin: 0;
  margin-top: 37px;
  box-sizing: border-box;
}
.program-form__submit {
  display: block;
  box-sizing: border-box;
  margin: 0 auto;
}
.program-form__button {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
  width: 100%;
  padding-top: 11px;
  padding-right: 26px;
  padding-bottom: 9px;
  padding-left: 26px;
  margin: 0 auto;
  background-color: #5eaa2f;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
}
.program-form__submit small {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  color: #000000;
  display: inline-block;
  width: 100%;
  text-align: center;
  margin-top: 23px;
}
.choose-form {
  margin-top: 39px;
}
.cat-info {
  width: 100%;
  box-sizing: border-box;
}
.cat-info__wrapper {
  display: flex;
  align-items: center;
  margin-top: 14px;
}
.cat-info__label {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
  text-transform: uppercase;
  flex-shrink: 0;
}
.cat-info__name,
.cat-info__weight,
.cat-info__age {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
  text-transform: uppercase;
  border: 1px solid #e7e7e7;
  padding-top: 13px;
  padding-bottom: 13px;
  padding-left: 16px;
  box-sizing: border-box;
  margin-left: 15px;
  width: 100%;
}
.program-target {
  width: 100%;
  border: none;
  padding: 20px 0;
  margin: 0;
  margin-top: 22px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
  box-sizing: border-box;
}
.program-target__label {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #222222;
  text-transform: uppercase;
}
.target-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.target-list__item {
  margin-bottom: 20px;
  padding-left: 40px;
}
.target-list__item:last-child {
  margin-bottom: 0;
}
.target-list__item label {
  position: relative;
  display: block;
  cursor: pointer;
  user-select: none;
  padding-top: 7px;
  color: #222222;
}
.target-list__input-radio + label::before {
  content: "";
  position: absolute;
  left: -43px;
  top: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  border-radius: 50%;
}
.target-list__input-radio:checked + label::after {
  content: "";
  position: absolute;
  top: 8px;
  left: -35px;
  width: 14px;
  height: 14px;
  background: #5eaa2f;
  border-radius: 50%;
}
.cat-owner {
  padding: 0;
  margin-top: 26px;
  box-sizing: border-box;
}
.cat-owner__inner {
  margin-top: 35px;
}
.cat-owner__wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.cat-owner__wrapper:last-child {
  margin-bottom: 0;
}
.cat-owner__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 26px;
  font-weight: 400;
  color: #000000;
  display: inline-block;
  width: 190px;
}
.cat-owner__label {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
  text-transform: uppercase;
  flex-shrink: 0;
}
.cat-owner__email,
.cat-owner__phone {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
  text-transform: uppercase;
  border: 1px solid #e7e7e7;
  padding-top: 13px;
  padding-bottom: 15px;
  padding-left: 16px;
  box-sizing: border-box;
  margin-left: 11px;
  width: 100%;
}
.comments {
  box-sizing: border-box;
  margin-top: 20px;
}
.comments__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 26px;
  font-weight: 400;
  color: #000000;
  margin-bottom: 40px;
  padding-right: 20px;
}
.comments__message {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
  text-transform: uppercase;
  border: 1px solid #e7e7e7;
  width: 100%;
  min-height: 150px;
  padding-top: 11px;
  padding-left: 15px;
  padding-right: 14px;
  padding-bottom: 10px;
  box-sizing: border-box;
}
.additionally {
  width: 100%;
  border: none;
  margin-top: 18px;
  box-sizing: border-box;
}
.additionally-list {
  list-style: none;
  padding: 0;
  margin: 0;
  padding-bottom: 22px;
  margin-top: 22px;
}
.additionally-list__item label {
  position: relative;
  display: block;
  cursor: pointer;
  user-select: none;
  padding-top: 4px;
  color: #222222;
}
.additionally-list__input-checkbox + label::before {
  content: "";
  position: absolute;
  left: -43px;
  top: 0;
  width: 26px;
  height: 26px;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
}
.additionally-list__input-checkbox:checked + label::after {
  content: "";
  position: absolute;
  top: 6px;
  left: -39px;
  width: 18px;
  height: 14px;
  background: url("../../img/check.svg");
}
.additionally-list__item {
  padding-left: 46px;
  padding-top: 9px;
  padding-bottom: 8px;
  margin-bottom: 8px;
  box-sizing: border-box;
}
.additionally-list__item:last-child {
  margin-bottom: 0;
}
.additionally-list__label {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #000000;
  text-transform: uppercase;
}
.direction {
  padding: 0 20px;
  margin: 0;
  margin-top: 22px;
}
.type-list {
  list-style: none;
  padding: 0;
  margin: 0;
  flex-wrap: wrap;
}
.type-list__item {
  width: 100%;
  box-sizing: border-box;
  padding: 30px 20px 23px 20px;
  margin-bottom: 20px;
  background-color: #f2f2f2;
}
.type-list__item a:hover {
  opacity: 0.9;
  cursor: pointer;
}
.type-list__item a:active,
.type-list__item a:focus {
  opacity: 0.3;
  cursor: pointer;
}
.type-list__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  color: #000000;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  text-align: center;
  position: relative;
}
.type-list__item--slim .type-list__title {
  padding-left: 15px;
}
.type-list__item--pro .type-list__title {
  padding-left: 39px;
  margin-top: -1px;
}
.type-list__item--slim .type-list__title::after {
  content: "";
  width: 50px;
  height: 53px;
  position: absolute;
  background: url("../../img/cat_back01.svg");
  background-repeat: no-repeat;
  right: auto;
  left: 2px;
  top: -9px;
}
.type-list__item--pro .type-list__title::after {
  content: "";
  width: 67px;
  height: 50px;
  position: absolute;
  background: url("../../img/cat_back02.svg");
  background-repeat: no-repeat;
  right: auto;
  left: -7px;
  top: -7px;
}
.type-list__item p {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
  margin-top: 29px;
  padding-bottom: 22px;
  margin-bottom: 19px;
  box-sizing: border-box;
  border-bottom: 1px solid #d9d9d9;
}
.type-list__item a {
  display: flex;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  text-transform: uppercase;
  color: #000000;
  background-color: transparent;
  justify-content: left;
  position: relative;
}
.type-list__item a::after {
  content: "";
  width: 24px;
  height: 12px;
  position: absolute;
  background: url("../../img/icon_arrow.svg");
  background-repeat: no-repeat;
  right: auto;
  left: 99px;
  top: 2px;
}
.scheme {
  margin-top: 25px;
  padding: 0 20px;
}
.scheme h2 {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  color: #000000;
  margin: 0;
  padding: 0;
}
.nutrition-list {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 38px;
}
.nutrition-list__item {
  display: flex;
  margin-bottom: 16px;
}
.nutrition-list__item img {
  width: 60px;
  height: 60px;
  background: #5eaa2f;
}
.nutrition-list__item p {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
  padding: 0;
  margin: 0;
  order: 2;
  margin-left: 20px;
  margin-top: 2px;
}
.example {
  background: #eaeaea;
  padding: 0 20px;
  margin-top: 39px;
  padding-bottom: 33px;
}
.example__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  color: #000000;
  margin: 0 auto;
  display: inline-block;
  margin-top: 28px;
}
.example__text {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
  margin-top: 33px;
  margin-bottom: 24px;
}
.example__table {
  display: flex;
  justify-content: space-between;
  margin: 0;
}
.example__column {
  width: calc(50% - 16px);
}
.example__column dt {
  position: relative;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  text-transform: uppercase;
  color: #000000;
  text-align: center;
  border-radius: 3px;
  border: 1px solid #cdcdcd;
  padding-top: 14px;
  padding-bottom: 17px;
}
.example__column dd {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  font-weight: 400;
  color: #444444;
  text-align: center;
  margin: 0 20px;
  padding: 0 15px;
  background: #eaeaea;
  transform: translateY(-9px);
}
.example__result {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  color: #000000;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  margin-top: 8px;
}
.example__controls {
  display: flex;
  justify-content: center;
  margin-top: 33px;
  padding-left: 5px;
  position: relative;
}
.example__scale {
  margin-top: 2px;
}
.example__toggle {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
}
.example__toggle--before {
  padding-right: 18px;
}
.example__toggle--after {
  padding-left: 18px;
}
.example__range {
  display: flex;
  flex-direction: column;
  margin-top: 35px;
}
.example__controls input[type="range"] {
  box-sizing: border-box;
  overflow: hidden;
  border-radius: 7px;
  border: 1px solid #cdcdcd;
  width: 84px;
  appearance: none;
  background: #ffffff;
}
.example__controls input[type="range"]::-webkit-slider-runnable-track {
  height: 14px;
  appearance: none;
  color: #5eaa2f;
  padding: 4px 6px;
}
.example__controls input[type="range"]::-webkit-slider-thumb {
  width: 35px;
  appearance: none;
  height: 6px;
  cursor: ew-resize;
  background: #5eaa2f;
  border-radius: 6px;
}
.example__controls input[type="range"]::-moz-range-progress {
  background: #5eaa2f;
}
.example__controls input[type="range"]::-moz-range-track {
  background: #ffffff;
}
.example__controls input[type="range"]::-ms-fill-lower {
  background: #5eaa2f;
}
.example__controls input[type="range"]::-ms-fill-upper {
  background: #ffffff;
}
.range-items {
  position: relative;
  display: block;
  width: 100%;
  height: 240px;
  left: -8px;
}
.range-items__item {
  position: absolute;
  width: 100%;
  height: 240px;
}
.range-items__item--before {
  display: block;
}
.range-items__item--mixed {
  display: none;
}
.range-items__item--after {
  display: none;
}
.products {
  padding-bottom: 23px;
}
.products__wrapper {
  display: flex;
  padding-bottom: 8px;
}
.products__container {
  display: block;
  padding: 0 20px;
}
.products__image {
  width: 50%;
  display: flex;
  justify-content: center;
}
.products__img {
  width: 140px;
  height: 112px;
}
.products__name {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #222222;
  text-transform: uppercase;
  text-align: left;
  margin-bottom: 13px;
  display: flex;
  flex-direction: column;
  padding: 0 60px 0 0;
}
.products__more {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #111111;
  text-transform: uppercase;
  display: inline-block;
  text-align: center;
  margin: 0;
  padding: 0;
  padding-bottom: 10px;
  position: relative;
}
.products__more::before,
.products__more::after {
  position: absolute;
  content: "";
  width: 2px;
  height: 30px;
  background: #d3d3d3;
  top: -50px;
  left: 50%;
  right: auto;
}
.products__more::before {
  transform: rotate(90deg);
}
.products__button {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
  width: 100%;
  padding: 11px 26px 9px;
  margin: 0 auto;
  background-color: #5eaa2f;
  display: flex;
  justify-content: center;
